import React, { useContext } from 'react'
import invoke from '../../utils/invoke'
import { MakeContext } from '../MakeContext'

export default function Welcome () {
  const make = useContext(MakeContext)
  const { setLayers, newLayer } = make
  const layers = newLayer ? [{}] : make.layers

  function goStamp () {
    layers[layers.length - 1].type = 'stamp'
    setLayers(layers)
    invoke('navigateTo', {
      routeName: !newLayer ? undefined : 'MakePage',
      to: `/make/stamp`
    })
  }
  return (
    <div style={{
      position: 'fixed',
      top: 0,
      right: 0,
      left: 0,
      bottom: 0,
      display: 'flex',
      backgroundColor: '#fff'
    }}>

      <div style={{
        display: 'flex',
        flex: 1,
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center'
      }}>
        <div style={{
          paddingLeft: 20,
          paddingRight: 20
        }}>
          <img src={require('../../assets/QuickMake-bg.png')} alt='' style={{
            width: '100%'
          }} />
        </div>
        <div style={{
        //   padding: 15,
          marginBottom: 70,
          marginTop: 34
        }}>
          <div style={{
            fontSize: 32,
            marginBottom: 22,
            color: '#1b1b1b',
            fontWeight: 'bold',
            textAlign: 'center'
          }}>Welcome to UTme!</div>
          <div style={{
            fontSize: 15,
            lineHeight: '22px',
            color: '#1b1b1b',
            fontWeight: 'normal',
            textAlign: 'center',
            fontFamily: 'HelveticaNeue'
          }}>
            UTme! lets you design your original UT<br />
            from your smartphone.
          </div>
        </div>
      </div>

      <div style={{
        position: 'fixed',
        bottom: 16,
        left: 15,
        right: 15
      }}>

        <div onClick={() => goStamp()}>
          <div style={{
            backgroundColor: '#1b1b1b',
            color: '#FFF',
            textAlign: 'center',
            fontWeight: 'bold',
            lineHeight: '36px',
            padding: 8,
            fontSize: '16px',
            letterSpacing: 0,
            height: 36,
            paddingLeft: 16,
            paddingRight: 16,
            fontFamily: 'UniqloPro-Bold'
          }}>
            <span>START</span>
          </div>
        </div>
      </div>
    </div>
  )
}
